import React, { useState, useEffect } from "react";

const Card = ({ title, summary, color, sources, keywords, categories }) => {

  const [open, setOpen] = useState(false);

  const clicked = () => {
    setOpen(!open);
  }

  return (
    <div style={{ display: "flex" }} onClick={clicked}>
      <div style={{ display: "flex", flex: "2 2 0", flexDirection: "column", backgroundColor: color }}>
        <div style={{ fontSize: "30px" }}>{title}</div>
        <div style={{ fontSize: "12px" }}>{keywords}</div>
        <div style={{ fontSize: "15px" }}>{summary}</div>
      </div>
      <div style={{ display: "flex", flex: "1 1 0", flexDirection: "column" }}>
        <div>
          <div>Related sources:</div>
          { sources && sources.length > 0 &&
            sources.map(source => {
              return <div>{source.link}</div>
            })
          }
        </div>
      </div>
    </div>
  );
}
 
export default Card;